<template>
    <div class="add-address" v-loading="loading">
        <el-form :model="addressForm"
                 :rules="addressRules"
                 :show-message="false"
                 size="small"
                 ref="addressForm"
                 label-width="110px"
                 label-position="left"
                 class="address-form">
            <el-form-item :label="$L('收货人')" prop="name">
                <el-input v-model="addressForm.name" :placeholder="$L('姓名')"></el-input>
            </el-form-item>
            <el-form-item :label="$L('手机号')" prop="mobile">
                <el-input v-model="addressForm.mobile" :placeholder="$L('手机号码')"></el-input>
            </el-form-item>
<!--            <el-form-item :label="$L('所在地区')" class="address" prop="regions">-->
<!--                <region-view style="width: 100%;" v-model="addressForm.regions" :placeholder="defaultCity"></region-view>-->
<!--            </el-form-item>-->
            <el-form-item :label="$L('所在地区')" prop="Continent">
                <el-select v-model="addressForm.Continent" :placeholder="$L('请选择')" @change="continentFunc">
                    <el-option
                            v-for="item in Continent"
                            :key="item.id"
                            :label="$lang() === 'lang_0' || $lang() === 'lang_1' ? item.name : item.name_en"
                            :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item :label="addressForm.addName" prop="country" v-if="countryData">
                <el-select v-model="addressForm.country" :placeholder="$L('请选择')" @change="countryFunc">
                    <el-option
                            v-for="item in Country"
                            :key="item.id"
                            :label="$lang() === 'lang_0' || $lang() === 'lang_1' ? item.name : item.name_en"
                            :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item :label="addressForm.addName" prop="Province" v-if="provinceData">
                <el-select v-model="addressForm.Province" :placeholder="$L('请选择')" @change="provinceFunc">
                    <el-option
                            v-for="item in Province"
                            :key="item.id"
                            :label="$lang() === 'lang_0' || $lang() === 'lang_1' ? item.name : item.name_en"
                            :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item :label="addressForm.addName" prop="city" v-if="cityData">
                <el-select v-model="addressForm.city" :placeholder="$L('请选择')" @change="cityFunc">
                    <el-option
                            v-for="item in City"
                            :key="item.id"
                            :label="$lang() === 'lang_0' || $lang() === 'lang_1' ? item.name : item.name_en"
                            :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item :label="$L('详细地址')" prop="address">
                <el-input v-model="addressForm.address" :placeholder="$L('输入道路、楼栋层或房间号等信息')"></el-input>
            </el-form-item>
            <el-form-item :label="$L('邮政编码')" prop="recCode">
                <el-input v-model="addressForm.recCode" :placeholder="$L('请输入邮政编码')"></el-input>
            </el-form-item>
            <el-form-item :label="$L('设为默认')">
                <el-switch v-model="addressForm.defaultAddress" :width="switchWidth" ></el-switch>
            </el-form-item>
            <div class="bottom-btns">
                <el-button type="primary" @click="save" class="button">{{ $L('保存') }}</el-button>
            </div>
        </el-form>
    </div>
</template>

<script>

    import RegionView from "../../../components/common/regionView";
    export default {
        name: "addAddress",
        components: {RegionView},
        data() {
            return {
                loading: true,
                switchWidth: 50,
                //如果是修改的，这里用于显示默认的城市信息
                defaultCity:'',
                addressForm: {
                    name: '',
                    mobile: '',
                    address: '',
                    defaultAddress: true,
                    regions: [],
                    Continent:'0',
                    country:'0',
                    Province:'0',
                    city:'0',
                    addName:'',
                    recCode:''
                },
                addressRules: {
                    name: [
                        {required: true, message: this.$L('请输入收货人姓名')},
                    ],
                    mobile: [
                        {
                            required: true,
                            validator: (rule, value, callback) => {
                                value = parseInt(value);
                                if(isNaN(value)){
                                    callback(new Error(this.$L('请选输入正确的手机号码或固定电话')));
                                }else{
                                    let patter = /^((1[3456789]\d{9})|(\d{6,16}))$/;
                                    if(!patter.test(value)){
                                        return callback(new Error(this.$L('手机号码或固定电话有误')));
                                    } else {
                                        callback();
                                    }

                                }
                            }
                        },
                    ],
                    address: [
                        {
                            required: true,
                            message: this.$L('请输入详细地址')
                        },
                    ],
                    Continent:[{required: true}],
                    country:[{required: false}],
                    Province:[{required: false}],
                    city:[{required: false}],
                },
                countryData:false,
                provinceData:false,
                cityData:false,
                Continent:[],
                Country:[],
                Province:[],
                City:[],


            }
        },
        created() {

            if (this.$route.params.id) {
                this.getData();
            } else {
                this.loading = false;
            }
            this.queryFunc();
        },

        methods: {
            save() {
                if (this.addressForm.name == '') {
                    this.$message({
                        message: this.$L('收货人姓名不能为空!'),
                        type: 'error'
                    });
                    return ;
                }
                if (this.addressForm.mobile == '') {
                    this.$message({
                        message: this.$L('手机号不能为空!'),
                        type: 'error'
                    });
                    return ;
                } else {
                    let value = parseInt(this.addressForm.mobile);
                    if(isNaN(value)){
                        this.$message({
                            message: this.$L('请选输入正确的手机号码或固定电话!'),
                            type: 'error'
                        });
                        return ;
                    }else{
                        let patter = /^((1[3456789]\d{9})|(\d{6,16}))$/;
                        if(!patter.test(value)){
                            this.$message({
                                message: this.$L('手机号码或固定电话有误!'),
                                type: 'error'
                            });
                            return ;
                        }

                    }

                }
                if (this.addressForm.address == '') {
                    this.$message({
                        message: this.$L('详细地址不能为空!'),
                        type: 'error'
                    });
                    return ;
                }
                if (this.addressForm.Continent == '') {
                    this.$message({
                        message: this.$L('所在地区不能为空!'),
                        type: 'error'
                    });
                    return ;
                }
                if (this.addressForm.country == '') {
                    this.$message({
                        message: this.$L('所在国家不能为空!'),
                        type: 'error'
                    });
                    return ;
                }
                if (this.addressForm.recCode == '') {
                    this.$message({
                        message: this.$L('邮政编码不能为空!'),
                        type: 'error'
                    });
                    return ;
                } else {
                    let value = parseInt(this.addressForm.recCode);
                    if(isNaN(value)){
                        this.$message({
                            message: this.$L('请输入正确的邮政编码'),
                            type: 'error'
                        });
                        return ;
                    }

                }
                this.$refs.addressForm.validate((valid) => {
                    if (valid) {
                        const _self = this;

                        //手机端的单独处理地区问题
                        let regions = [];
                        if (_self.addressForm.Continent && _self.addressForm.Continent != 0 && !isNaN(_self.addressForm.Continent)) regions.push(_self.addressForm.Continent);
                        if (_self.addressForm.country && _self.addressForm.country != 0 && !isNaN(_self.addressForm.country)) regions.push(_self.addressForm.country);
                        if (_self.addressForm.Province && _self.addressForm.Province != 0 && !isNaN(_self.addressForm.Province)) regions.push(_self.addressForm.Province);
                        if (_self.addressForm.city && _self.addressForm.city != 0 && !isNaN(_self.addressForm.city)) regions.push(_self.addressForm.city);
                        if (regions.length>0) {
                            _self.addressForm.regions = regions;
                        }

                        let arr = {
                            idRec : _self.$route.params.id,
                            recName :  _self.addressForm.name,
                            regions :  _self.addressForm.regions,
                            recAddress :  _self.addressForm.address,
                            recMobile : _self.addressForm.mobile,
                            recCode :  _self.addressForm.recCode,
                            isDefault :  _self.addressForm.defaultAddress == true ? 1 : 0,
                            lang:'',
                        };
                        _self.$api('editAddress', arr, function (reData) {
                            if (reData.flag === 1) {
                                // _self.reload();
                                _self.$router.push({name:'addressList'});
                                _self.loading = false;
                            } else if (reData.flag === -1) {
                                //登录超时了
                                _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                    confirmButtonText: _self.$L('重新登录'),
                                    cancelButtonText: _self.$L('取消'),
                                    type: 'error',
                                    distinguishCancelAndClose: true
                                }).then(() => {
                                    _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                                }).catch(action => {
                                    _self.$gotoWebHome();
                                });
                            } else {
                                //未知错误
                                _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                    confirmButtonText: _self.$L('点击重试'),
                                    cancelButtonText: _self.$L('返回首页'),
                                    type: 'error',
                                    distinguishCancelAndClose: true
                                }).then(() => {
                                    _self.getData();
                                }).catch(action => {
                                    _self.$router.push({name: 'addressList'});
                                });
                            }
                        }, function (error) {
                            //网络请求错误
                            _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.$router.push({name: 'addressList'});
                            });
                        });
                    } else {
                        return false;
                    }
                });
            },
            getData() {
                const _self = this;
                _self.loading = true;
                if (_self.$route.params.id !== '') {
                    _self.$api('getAddressInfo', {idRec: _self.$route.params.id}, function (reData) {
                        if (reData.flag === 1) {
                            _self.addressForm.name = reData.data.name;
                            _self.addressForm.mobile = reData.data.mobile;
                            _self.addressForm.defaultAddress = reData.data.isDefault === 1 ? true : false;
                            _self.addressForm.address = reData.data.address;
                            _self.addressForm.recCode = reData.data.recCode;
                            //用于默认显示
                            _self.addressForm.Continent = reData.data.regionsName;
                            //默认的地址信息，让程序去数据库加字段存放
                            _self.addressForm.regions = !reData.data.regions ? [1,7,247,3021] : reData.data.regions;

                            _self.loading = false;
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.$router.push({name: 'addressList'});
                            });
                        }
                    }, function (error) {
                        //网络请求错误
                        _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'addressList'});
                        });
                    });
                } else {
                    _self.loading = false;
                }
            },
            continentFunc(val){
                this.countryData = true;
                let pid = 0;
                if(val){
                    pid = val;
                }
                const _self = this;
                _self.$api('getRegion', {pid: pid}, function (reData) {
                    if (reData.flag === 1) {
                        _self.Country=reData.data;

                        _self.provinceData = false;
                        _self.addressForm.country = '';
                        _self.cityData = false;
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'addressList'});
                    });
                });

            },
            countryFunc(val){
                let pid = 0;
                if(val){
                    pid = val;
                }
                const _self = this;
                _self.$api('getRegion', {pid: pid}, function (reData) {
                    if (reData.flag === 1) {
                        _self.Province=reData.data;
                        _self.provinceData = _self.Province.length>1 ?  true : false;
                        _self.addressForm.Province = '';
                        _self.cityData = false;
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'addressList'});
                    });
                });
            },
            provinceFunc(val){
                let pid = 0;
                if(val){
                    pid = val;
                }
                const _self = this;
                _self.$api('getRegion', {pid: pid}, function (reData) {
                    if (reData.flag === 1) {
                        _self.City=reData.data;
                        _self.cityData = _self.City.length>1 ?  true : false;
                        _self.addressForm.city = '';

                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'addressList'});
                    });
                });


            },
            cityFunc(val){

            },

            queryFunc(val){

                const _self = this;
                _self.$api('getRegion', {}, function (reData) {
                    if (reData.flag === 1) {
                        _self.Continent=reData.data;
                        if (!_self.$route.params.id) {
                            _self.addressForm.Continent = '';
                        }
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'addressList'});
                    });
                });

            }
        }
    }
</script>

<style lang="scss" scoped>
    .add-address{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #f2f2f2;
        .address-form{
            background: #fff;
            /deep/ .el-form-item{
                border-bottom: 1px solid #ddd;
                padding: 10px;
                margin-bottom: 0;
            }
            /deep/ .el-input__inner{
                border:none;
            }
            .bottom-btns{
                padding: 60px 10px 10px;
                border-bottom: 1px solid #ddd;
                .button{
                    width: 100%;
                }
            }
        }

    }
</style>
<style scoped>
    .el-message-box {
        width: 94% !important;
    }
</style>